/*main card configuration*/
.card-content{
	position:  relative;
	width: 300px;
	height: 400px;
	background-color: #9eebcf;
	display:  inline-block;
	border-radius: .5rem;
	margin: .25rem;
	box-shadow: .25rem;
	border-width: .25rem;
	transition: all 3s ease;
}
/*Full details section*/
.information{
	position: absolute;
	width:  100%;
	height:  100%;
	Opacity: 0;
	text-align: left;
	padding: 1rem 1rem 1rem .5rem;
	transform: translateY(-70%);
}

.card-content:hover .information{
	transition: all 2s ease;
	Opacity: 1;
}

/*Robot image section*/
.card-content .robo-image{
	transition: all 1s ease;
	transform: translateY(3px);
	opacity: 1;
}

.card-content:hover .robo-image{
	transition: all 1s ease;
	opacity: 0.2;
	transform: translateY(3px) translateX(3.5rem);
	/*transform: scale(0.4) translateX(80%) translateY(-90%);*/
}
/*Default information section*/
.card-content .default-info{
	display: block;
	background-color: black;
	color: white;
	padding: 1rem 0 1rem 0;
	border-radius: 0 0 0.5rem 0.5rem;
	transition: all 2s ease;
}
.card-content:hover .default-info{
	transition: all 2s ease;
	display: none;
}

/*Fixing the position of icons*/

.information p{
	display: flex;
	flex-wrap: nowrap;
}
.information box-icon{
margin-right: 5px;
}
.information box-icon:hover{
	animation:tada 1.5s ease infinite;
}
@keyframes tada{from{-webkit-transform:scale3d(1,1,1);transform:scale3d(1,1,1)}10%,20%{-webkit-transform:scale3d(.95,.95,.95) rotate3d(0,0,1,-10deg);transform:scale3d(.95,.95,.95) rotate3d(0,0,1,-10deg)}30%,50%,70%,90%{-webkit-transform:scale3d(1,1,1) rotate3d(0,0,1,10deg);transform:scale3d(1,1,1) rotate3d(0,0,1,10deg)}40%,60%,80%{-webkit-transform:rotate3d(0,0,1,-10deg);transform:rotate3d(0,0,1,-10deg)}to{-webkit-transform:scale3d(1,1,1);transform:scale3d(1,1,1)}}.bx-spin{-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite}.bx-spin-hover:hover{-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite}








/*Toolkit section*/
.toolkit{
	position: absolute;
	border: 1px solid black;
	font-size: 12px;
	padding: 0rem .2rem;
	margin: .5em;
	transform: translateY(-1.40rem);
	background-color: #DDDDDD;
	border-radius: 1em;
	font-weight: 500;
	opacity: 0;
	pointer-events: none;
}
box-icon:hover+.toolkit{
	opacity: 1;
	transition: all 1s ease;
}

